import React,{useState} from 'react';

import quehack from '../../img/quehack.png';

function FlipCard(props){
    const [flip, setFlip] = useState(false);

    return(
        <div className="work_row">
            <div className="external" onClick={()=>{setFlip(!flip)}}>
                <div className={(flip && props.num%2 == 0) ? 'inner oddflip' : (flip && props.num%2 == 1) ? 'inner evenflip' : 'inner'}>
                    <div className="front" style={{backgroundImage:`url(${props.subImg})`}}>
                    </div>
                    <div className="back">
                    <div className="desc">{props.desc}</div>
                        <div className="tools">Built with {props.tools}</div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default FlipCard