import React from 'react';
import {changeDisplay} from '../../redux/actions';
import {connect} from 'react-redux';

function WorkCard(props){

    var viewmore_class;
    (props.work_name === props.name) ? viewmore_class = 'viewmore_display' : viewmore_class = 'viewmore_none';

    var onclick = (
        <div style={{backgroundImage:`url(${props.img_path})`}} className="small_work_img" onClick={()=>{
            props.changeDisplay(props.name, props.img_path,'hide_big_work_img deplay');
            window.setTimeout(()=>{
                props.changeDisplay(props.name, props.img_path,'show_big_work_img');
            },100)
        }} >
        <div className={viewmore_class}>
            <a target="_blank" rel="github" href={props.github ? props.github : props.link}>{props.github ? 'Github' : 'Website'}</a>
        </div>
        </div>
    );
    var hover = (
            <div style={{backgroundImage:`url(${props.img_path})`}} className="small_work_img" onMouseOverCapture={()=>{
                props.changeDisplay(props.name, props.img_path,'show_big_work_img');
            }} onMouseOutCapture={()=>{
                props.changeDisplay(props.name, props.img_path,'hide_big_work_img');
            }}>
                <div className="viewmore">
                    <a target="_blank" rel="github" href={props.github ? props.github : props.link}>{props.github ? 'Github' : 'Website'}</a>
                </div>
            </div>
    );

    return(
        <div className="small_work">
            {(props.screensize.width > 1024) ? hover : onclick}
        </div>
    )
}

const mapStateToProps = state => {
    return {
        display:state.display,
        work_class:state.work_class,
        work_name:state.name,
        screensize:state.screensize
    }
}

export default connect(mapStateToProps,{changeDisplay})(WorkCard);